body .is-layout-constrained .wp-block-blocks-gamestore-block-hero {
	position: relative;
	width: 100vw !important;
	height: 980px;
	max-width: 100vw !important;
	margin-right: calc(var(--wp--style--root--padding-right) * -1) !important;
	margin-left: calc(var(--wp--style--root--padding-left) * -1) !important;
}

.wp-block-blocks-gamestore-block-hero {
	.image-bg {
		position: absolute;
		top: 0;
		left: 0;
		width: 100vw;
		height: 980px;
		object-fit: cover;
		z-index: 0;
	}
	.video-bg {
		position: absolute;
		top: 0;
		left: 0;
		width: 100vw;
		height: 980px;
		object-fit: cover;
		z-index: 0;
	}

	.hero-mask {
		width: 80%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		background: var(--mask-gradient);
		z-index: 1;
	}

	.hero-content {
		position: relative;
		width: 100%;
		height: 100%;
		max-width: 1540px;
		margin-left: auto;
		margin-right: auto;

		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: flex-start;
		gap: 24px;
		z-index: 2;

		padding-left: var(--wp--style--root--padding-left);
		padding-right: var(--wp--style--root--padding-right);

		.hero-title {
			color: var(--white-contrast);
			strong {
				background: var(
					--accent-gradient,
					linear-gradient(90deg, #3dafe2 0%, #6f76c0 100%)
				);
				background-clip: text;
				-webkit-background-clip: text;
				-webkit-text-fill-color: transparent;
			}
		}
	}
	.hero-slider {
		position: absolute;
		width: 100%;
		height: 150px;
		left: 0;
		bottom: 0;
		display: flex;
		align-items: center;
		justify-content: center;
		background: var(
			--mask-gradient-second,
			linear-gradient(180deg, rgba(250, 250, 250, 0) 0%, #fafafa 100%)
		);
		z-index: 3;

		.slider-container {
			width: 100%;
			max-width: 1540px;
			margin-left: auto;
			margin-right: auto;
			overflow: hidden;
			box-sizing: border-box;

			.swiper-wrapper {
				display: flex;

				.slide-item {
					margin-right: 100px;
					max-width: fit-content;
					margin-left: 0;

					img.light-logo {
						display: none;
					}
				}
			}
		}
	}
}

.dark-mode-gamestore {
	.wp-block-blocks-gamestore-block-hero
		.hero-slider
		.slider-container
		.swiper-wrapper
		.slide-item {
		img.dark-logo {
			display: none;
		}
	}
	.wp-block-blocks-gamestore-block-hero
		.hero-slider
		.slider-container
		.swiper-wrapper
		.slide-item {
		img.light-logo {
			display: block;
		}
	}
}
